h1, h2, h3,
.h1, .h2, .h3 {
  margin-bottom: $headings-margin-bottom;
  font-family: "MavenPro";
  font-weight: 300;
  line-height: $headings-line-height;
  color: $headings-color;
}

h4, h5, h6,
.h4, .h5, .h6 {
  margin-bottom: $headings-margin-bottom;
  font-family: "MavenPro";
  font-weight: 400;
  line-height: $headings-line-height;
  color: $headings-color;
  text-transform: uppercase;
}

.heading {
  font-size: 1.6rem;
  text-transform: uppercase;
}
